.goodsDetails{
    background:#fff;
}
.goodsDetails > div > img{
    width: 100%;
}
.description{
    padding: 0 10px 5px;
}
.description p{
    font-size: 16px;
}
.description p:nth-child(2){
    color: #e4393c;
}
.price{
    font-size: 18px;
}
.description p:nth-child(3){
    font-size: 14px;
    color: #888;
    display: flex;
    justify-content:space-between;
}
.goodsDetails_go{
    width: 100%;
    height: 50px;
    display: flex;
    position: fixed;
    left: 0;
    bottom: 0;
}
.goodsDetails_go span{
    display: inline-block;
    width: 50%;
    height: 100%;
    color: #fff;
}
.goodsDetails_go span:nth-child(1){
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #F0CAB6;
}
.goodsDetails_go span:nth-child(2){
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #F40;
}